<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <form>
          <div>
            <div class="row">
              <div class="col-md-1">
                <fg-input>
                  <el-input
                    type="search"
                    class="mb-6"
                    clearable
                    prefix-icon="el-icon-search"
                    style="width: 200px"
                    placeholder="Search records"
                    v-model="searchQuery"
                    aria-controls="datatables"
                  ></el-input>
                </fg-input>
              </div>
            </div>
          </div>

          <div class="row">
            <label class="col-md-1 col-form-label text-right">STATUS</label>
            <n-radio class="display-inline" v-model="enabledRadio" label="ACTIVE">ACTIVE</n-radio>
            <div class="col-md-1">
              <n-radio v-model="enabledRadio" label="INACTIVE">INACTIVE</n-radio>
            </div>
          </div>
        </form>
      </card>
      <card>
        <div slot="header">
          <h5 class="title">FACILITIES</h5>
        </div>
        <div class="col-12 d-flex align-items-end flex-column">
          <el-select
            class="select-primary p-2"
            style="width: 200px"
            v-model="pagination.perPage"
            placeholder="Per page"
          >
            <el-option
              class="select-default"
              v-for="item in pagination.perPageOptions"
              :key="item"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </div>
        <el-table stripe style="width: 100%;" :data="queriedData" @row-click="onRowClick">
          <el-table-column width="100" type="index" align="center"></el-table-column>
          <el-table-column min-width="150" prop="name" label="NAME" align="center"></el-table-column>
          <el-table-column min-width="100" prop="fees" label="FEES" align="center"></el-table-column>
          <el-table-column min-width="100" prop="status" label="STATUS" align="center"></el-table-column>
        </el-table>
        <div
          slot="footer"
          class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
        >
          <div class>
            <p class="card-category">Showing {{from + 1}} to {{to}} of {{total}} entries</p>
          </div>
          <n-pagination
            class="pagination-no-border"
            v-model="pagination.currentPage"
            :per-page="pagination.perPage"
            :total="total"
          ></n-pagination>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
import { Radio } from "src/components";
import { Table, TableColumn, Select, Option } from "element-ui";
import { Pagination as NPagination } from "src/components";
import Fuse from "fuse.js";
import swal from "sweetalert2";

export default {
  components: {
    [Radio.name]: Radio,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    NPagination,
    [Select.name]: Select,
    [Option.name]: Option
  },
  computed: {
    /***
     * Returns a page from the searched data or the whole data. Search is performed in the watch section below
     */
    queriedData() {
      let result = this.facilities;
      if (this.searchedData.length > 0) {
        result = this.searchedData;
      }
      return result.slice(this.from, this.to);
    },
    to() {
      let highBound = this.from + this.pagination.perPage;
      if (this.total < highBound) {
        highBound = this.total;
      }
      return highBound;
    },
    from() {
      return this.pagination.perPage * (this.pagination.currentPage - 1);
    },
    total() {
      return this.searchedData.length > 0
        ? this.searchedData.length
        : this.facilities.length;
    }
  },
  data() {
    return {
      facilities: [],
      enabledRadio: "INACTIVE",
      pagination: {
        perPage: 5,
        currentPage: 1,
        perPageOptions: [5, 10, 25, 50],
        total: 0
      },
      searchQuery: "",
      propsToSearch: ["name"],
      searchedData: [],
      fuseSearch: null
    };
  },
  methods: {
    //click row to locate to edit facilities
    onRowClick(event) {
      window.location.href = `#/facilities/editfacilities/${event.id}`;
    },
    get(er) {
      this.$http
        .get("https://admindashboard-b2ab9.firebaseio.com/facilities.json")
        .then(function(data) {
          return data.json();
        })
        .then(function(data) {
          if (er == "INACTIVE") {
            var inactive_Array = [];
            for (let key in data) {
              if (data[key].status == er) {
                data[key].id = key;
                inactive_Array.push(data[key]);
              }
            }
            this.facilities = inactive_Array;
            //console.log(inactive_Array)
          } else {
            var active_Array = [];
            for (let key in data) {
              if (data[key].status == er) {
                data[key].id = key; //set key as id
                active_Array.push(data[key]); //push retrieve content into array
              }
            }
            this.facilities = active_Array;
            //console.log(active_Array)
          }
        });
    }
  },
  watch: {
    /**
     * Searches through the table data by a given query.
     * NOTE: If you have a lot of data, it's recommended to do the search on the Server Side and only display the results here.
     * @param value of the query
     */
    searchQuery(value) {
      let result = this.facilities;
      if (value !== "") {
        result = this.fuseSearch.search(this.searchQuery);
      }
      this.searchedData = result;
    }
  },
  //get request
  created() {
    this.$http
      .get("https://admindashboard-b2ab9.firebaseio.com/facilities.json")
      .then(function(data) {
        return data.json();
      })
      .then(function(data) {
        var fac_Array = [];
        for (let key in data) {
          data[key].id = key;
          fac_Array.push(data[key]);
        }
        this.facilities = fac_Array;
        this.fuseSearch = new Fuse(this.facilities, {
          keys: ["name"],
          threshold: 0.3
        });
      });
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
</style>
